import React from 'react';
import companyImg from '../../images/company.png';
import translatingImg from '../../images/translating.png';
import controlImg from '../../images/search.png';
import socialMediaImg from '../../images/social-media.png';
import administrationImg from '../../images/calendar.png';
import organizationImg from '../../images/process.png';
import homeImg from '../../images/home.png';
import accountingImg from '../../images/accounting.png';
import callImg from '../../images/call-center.png';
import './Services.css';



function Services(props: {click: boolean, setClick: React.Dispatch<React.SetStateAction<boolean>>}) {
  
  return (
    <div className='services'>
      <div className='services-title main-title'>Sluby</div>
      <div className="services-container services-container--desctop">

        {!props.click && <div className='services-home services-box'>
          <img src={homeImg} alt="home" className="services-icon" />
          <div className='title'>Prenjom sdla</div>
          <p>
            Virtulne sdlo obubuj hlavne drobn ivnostnci i spolonosti sruenm obmedzenm. Je to
            idelna alternatva, ak nemte fyzick kancelriu alebo prevdzku anechcete ma tto oficilnu
            podnikatesk adresu uVs doma.
            Toto <span>sdlo sa zapisuje do Obchodnho registra </span>spolonosti (miesto podnikania sa zapisuje do
            ivnostenskho registra ivnostnka) adan subjekt ho pouva na vetkch radnch dokumentoch.
          </p>
        </div>}

        {!props.click && <div className='services-accounting services-box'>
          <img src={accountingImg} className="services-icon" />
          <div className='title'>tovnctvo</div>
          <p>Ponuka <span>vedenia tovnctva</span> pre fyzick aprvnick osoby, <span>spracovanie daovch priznan</span>,
            ekonomicko  tovn <span>poradenstvo</span> na eliminciu monch rizk svisiacich spodnikanm.
            <br/><span> Sprva bytovch a nebytovch objektov</span>, vrtane spracovania ronch vytovan spotreby energi.</p>
        </div>}

        <div className='services-call services-box' id="cube" onClick={() => props.setClick(!props.click)}>
          <img src={callImg} className="services-icon" />
          <div className='title'>Virtulna asistentka</div>
          <p><span>Efektvne nahrad zamestnanca</span> vo Vaej firme a<span>uetr Vm stovky eur</span>. <span>Preberie za Vs lohy</span>, ktor
            nesthate alebo neviete a<span>zaplatte za u len toko, koko hodn vmesiaci ju relne potrebujete</span>.
            Nemuste jej dva mesan mzdu, ani plati za u odvody.
          </p><button className="click" id="click" onClick={() => props.setClick(!props.click)}>{!props.click && `Bliie informcie` || props.click && `Sp`}</button>
        </div>

        {props.click && <div className="assistant-container">
          <div className="assistant-item company"><img src={companyImg} className='assestant-img' /><p><span className="green">rady<br/></span>&#9900; Vyzdvihn / poda rzne dokumenty na radoch<br/>(okres ZH, ZV, BB, ZC, BS)</p></div>
          <div className="assistant-item administration"><img src={administrationImg} className='assestant-img' /><p><span className="green">Administratva<br/></span>&#9900; Pripravi rzne iadosti, oznmenia, pozvnky<br/>
          &#9900; Napsa rzne zmluvy (kpne, njomn, ...)<br/>
          &#9900; Vystavi faktry<br/>
          &#9900; Prepsa text</p></div>
          <div className="assistant-item social-media"><img src={socialMediaImg} className='assestant-img' /><p><span className="green">Socilne siete<br/></span>&#9900; Spravova socilne siete a web strnky<br/>
          &#9900; Online marketing<br/>
          &#9900; Vytvori jednoduch web strnky</p></div>
          <div className="assistant-item organization"><img src={organizationImg} className='assestant-img' /><p><span className="green">Organizcia<br/></span>&#9900; Zorganizova pracovn porady<br/>
          &#9900; Zabezpei kolenia<br/>
          &#9900; Rezervcia ubytovania 
</p></div>
          <div className="assistant-item control"><img src={controlImg} className='assestant-img' /><p><span className="green">Kontrola<br/></span>&#9900; Stri dleit termny, aby ste na ni nezabudli<br/>
          &#9900; Kontrola hrady faktr
</p></div>
          <div className="assistant-item translating"><img src={translatingImg} className='assestant-img' /><p><span className="green">Preklady<br/></span>&#9900; Preloenie psanch textov z / do anglickho jazyka</p></div>
        </div>}
      </div>

      <div className="services-container services-container--mobile">

        {<div className='services-home services-box'>
          <img src={homeImg} alt="home" className="services-icon" />
          <div className='title'>Prenjom sdla</div>
          <p className='services-description'>
            Virtulne sdlo obubuj hlavne drobn ivnostnci i spolonosti sruenm obmedzenm. Je to
            idelna alternatva, ak nemte fyzick kancelriu alebo prevdzku anechcete ma tto oficilnu
            podnikatesk adresu uVs doma.
            Toto <span>sdlo sa zapisuje do Obchodnho registra </span>spolonosti (miesto podnikania sa zapisuje do
            ivnostenskho registra ivnostnka) adan subjekt ho pouva na vetkch radnch dokumentoch.
          </p>
        </div>}

        {<div className='services-accounting services-box'>
          <img src={accountingImg} className="services-icon" />
          <div className='title'>tovnctvo</div>
          <p className='services-description'>Ponuka <span>vedenia tovnctva</span> pre fyzick aprvnick osoby, <span>spracovanie daovch priznan</span>,
            ekonomicko  tovn <span>poradenstvo</span> na eliminciu monch rizk svisiacich spodnikanm.
            <span> Sprva bytovch a nebytovch objektov</span>, vrtane spracovania ronch vytovan spotreby energi.</p>
        </div>}

        <div className='services-call services-box' id="cube" onClick={() => props.setClick(!props.click)}>
          <img src={callImg} className="services-icon" />
          <div className='title'>Virtulna asistentka</div>
          <p className='services-description'><span>Efektvne nahrad zamestnanca</span> vo Vaej firme a<span>uetr Vm stovky eur</span>. <span>Preberie za Vs lohy</span>, ktor
            nesthate alebo neviete a<span>zaplatte za u len toko, koko hodn vmesiaci ju relne potrebujete</span>.
            Nemuste jej dva mesan mzdu, ani plati za u odvody.
          </p><button className="click" id="click" onClick={() => props.setClick(!props.click)}>{!props.click && `Bliie informcie` || props.click && `Skry`}</button>
          {props.click && <div className="assistant-container">
          <div className="assistant-item company"><img src={companyImg} className='assestant-img' /><p className='services-description'><span className="green">Urady.</span> Vyzdvihn / poda rzne dokumenty na radoch
            (napr. list vlastnctva, vpis z obchodnho registra, iados o
            stavebn povolenie, ...)</p></div>
          <div className="assistant-item administration"><img src={administrationImg} className='assestant-img' /><p className='services-description'><span className="green">Administratva.</span> Prepsanie textu. Napsanie rznych iadost, oznmen, pozvnok
            Vystavenie faktr
            ...</p></div>
          <div className="assistant-item social-media"><img src={socialMediaImg} className='assestant-img' /><p className='services-description'><span className="green">Socilne siete.</span> Sprva socilnych siet,
            Pomoc so sprvou webovej strnky
            (napr. dopanie / aktualizovanie produktov, reklamy)</p></div>
          <div className="assistant-item organization"><img src={organizationImg} className='assestant-img' /><p className='services-description'><span className="green">Organizcia.</span> Zabezpeenie kolen
            Zorganizovanie pracovnch pord
            Rezervcia ubytovania pre obchodnch partnerov
            ...</p></div>
          <div className="assistant-item control"><img src={controlImg} className='assestant-img' /><p className='services-description'><span className="green">Kontrola.</span> Stri dleit termny, aby ste na ni nezabudli
            Kontrola hrady faktr
            ...</p></div>
          <div className="assistant-item translating"><img src={translatingImg} className='assestant-img' /><p className='services-description'><span className="green">Preklady.</span> Preloenie psanch textov z / do anglickho jazyka</p></div>
        </div>}
  
        </div>

            </div>
  
    </div>
  );
}

export default Services;
