import React, { useState } from 'react';
import { useNavigate } from "react-router-dom";
import yes from '../../images/yes.png';
import no from '../../images/no.png';
import './PriceList.css';
import { handleClickScrollToTop } from '../../App';

function PriceList(props: {click: boolean}) {
  const navigate = useNavigate();

  const navigateToPage = (url: string) => {
    navigate(url);
    handleClickScrollToTop();
  };

  const selectButtonPath = (option: number) => {
    if (option == 5) {
      return '/formular-zakladny'
    } else if (option == 8) {
      return 'formular-stredny'
    } else {
      return 'formular-rozsireny'
    }
  }

  const [option, setOption] = useState(5);

  return (
      <div className='prices'>
        <div className='prices-title main-title'>Cennk</div> 
        {!props.click && 
        <div className='desctop-table'>
        <div className='prices-box'>
          <ul className='services-list'>
            <li className='services-item services-item--first'>Balk sluieb svisiacich sprenjmom sdla *</li>
            <li className='services-item'>Shlas so zriadenm sdla / miesta podnikania **</li>
            <li className='services-item'>Oznaenie potovej schrnky</li>
            <li className='services-item'>Preberanie listovch zsielok (obyajnch)</li>
            <li className='services-item'>Archivcia neprevzatch dokladov max. 1 rok</li>
            <li className='services-item'>Preposlanie listovch zsielok mailom (neotvoren)  <span id='small-item-long' style={{
          fontSize: '0.8rem',
          fontWeight: 'unset',
        }}>1x tdenne</span></li>
            <li className='services-item'>Preposlanie lstku ouloen zsielky na pote mailom  <span id='small-item-long' style={{
          fontSize: '0.8rem',
          fontWeight: 'unset',
        }}>1x tdenne</span></li>
            <li className='services-item servise-item--long'>Preposlanie listovch zsielok na dohodnut adresu (doporuene)  <span id='small-item-long' style={{
          fontSize: '0.8rem',
          fontWeight: 'unset',
        }}>2x mesane</span></li>
            <li className='services-item'>Preposlanie obsahu listovch zsielok mailom (otvoren)  <span id='small-item-long' style={{
          fontSize: '0.8rem',
          fontWeight: 'unset',
        }}>1x tdenne</span></li>
            <li className='services-item services-item--last'>Cena spolu / mesiac</li>
          </ul>

          <ul className='check-list-01'>
            <li className='check-item check-item--first'>Zkladn</li>
            <li className='check-item '><img className='check-img' src={yes} /></li>
            <li className='check-item '><img className='check-img' src={yes} /></li>
            <li className='check-item'><img className='check-img' src={yes} /></li>
            <li className='check-item'><img className='check-img' src={yes} /></li>
            <li className='check-item'><img className='check-img' src={no} /></li>
            <li className='check-item'><img className='check-img' src={no} /></li>
            <li className='check-item'><img className='check-img' src={no} /></li>
            <li className='check-item'><img className='check-img' src={no} /></li>
            <li className='check-item check-item--last'>5 &euro;</li>
            <li className='check-item'><button className='click check-click' onClick={ () => navigateToPage("/formular-zakladny")}>Mm zujem</button></li>
          </ul>

          <ul className='check-list-02'>
            <li className='check-item check-item--first'>Stredn</li>
            <li className='check-item'><img className='check-img' src={yes} /></li>
            <li className='check-item'><img className='check-img' src={yes} /></li>
            <li className='check-item'><img className='check-img' src={yes} /></li>
            <li className='check-item'><img className='check-img' src={yes} /></li>
            <li className='check-item'><img className='check-img' src={yes} /></li>
            <li className='check-item'><img className='check-img' src={yes} /></li>
            <li className='check-item'><img className='check-img' src={no} /></li>
            <li className='check-item'><img className='check-img' src={no} /></li>
            <li className='check-item check-item--last'>8 &euro;</li>
            <li className='check-item'><button className='click check-click' onClick={ () => navigateToPage("/formular-stredny")}>Mm zujem</button></li>
          </ul>

          <ul className='check-list-03'>
            <li className='check-item check-item--first'>Rozren</li>
            <li className='check-item'><img className='check-img' src={yes} /></li>
            <li className='check-item'><img className='check-img' src={yes} /></li>
            <li className='check-item'><img className='check-img' src={yes} /></li>
            <li className='check-item'><img className='check-img' src={yes} /></li>
            <li className='check-item'><img className='check-img' src={yes} /></li>
            <li className='check-item'><img className='check-img' src={yes} /></li>
            <li className='check-item'><img className='check-img' src={yes} /></li>
            <li className='check-item'><img className='check-img' src={yes} /></li>
            <li className='check-item check-item--last'>13 &euro;</li>
            <li className='check-item'><button className='click check-click' onClick={ () => navigateToPage("/formular-rozsireny")}>Mm zujem</button></li>
          </ul>

          </div>
          <p className='prices-ps'>* Plat sa paulne rone, vopred</p>
          <p className='prices-ps'>** Registrcia sdla / miesta podnikania min. na 12 mesiacov</p>
        </div>}
  

      <div className='assistantPrice-box desctop-table'>
        <div className='assistantPrice-item services-item--first'>Vedenie utovnctva a sluby virtulnej asistentky</div>
        <div className='assistantPrice-item'>Cennk je stanoven individulne po zadan loh a zistenia nronosti prce ***</div>
      </div>
      <div className='desctop-table'>
      <p className='prices-ps' id="quotes">*** Ak sluba podlieha nkladom vynaloench predom (napr. poplatok na katastri), tieto poplatky su vybran od klienta predom.<br/> V ostatnch prpadoch je faktra dodan a po vykonan prc.</p>
      </div>


      <div className='mobile-table mobile-display'>
            <div className='service-item-mobile services-item--first'>Balk sluieb svisiacich sprenjmom sdla *</div>
            <div className='services-options service-item-mobile'>
              <div className={`service-item-click color-5 ${ (option==5)? "active" : ""}`} onClick={() => setOption(5)}>Zkladn</div>
              <div className={`service-item-click color-8 ${ (option==8)? "active" : ""}`} onClick={() => setOption(8)}>Stredn</div>
              <div className={`service-item-click color-13 ${ (option==13)? "active" : ""}`} onClick={() => setOption(13)}>Rozren</div>
            </div>

            <div className={`services-list-mobile color-${option}` }>
              <div className='service-item-mobile'>Shlas so zriadenm sdla / miesta podnikania **</div>
              <div className='service-item-mobile'>Oznaenie potovej schrnky</div>
              <div className='service-item-mobile'>Preberanie listovch zsielok (obyajnch)</div>
              <div className='service-item-mobile'>Archivcia neprevzatch dokladov max. 1 rok</div>
              {option!==5 && <div>
                <div className='service-item-mobile'>Preposlanie listovch zsielok mailom (neotvoren)  <span id='small-item-long' style={{
          fontSize: '0.8rem',
          fontWeight: 'unset',
        }}>1x tdenne</span></div>
                <div className='service-item-mobile'>Preposlanie lstku ouloen zsielky na pote mailom  <span id='small-item-long' style={{
          fontSize: '0.8rem',
          fontWeight: 'unset',
        }}>1x tdenne</span></div>
                {option==13 && <div> <div className='service-item-mobile'>Preposlanie listovch zsielok na dohodnut adresu (doporuene)  <span style={{
          fontSize: '0.8rem',
          fontWeight: 'unset',
        }} id='small-item-long'>2x mesane</span></div>
                <div className='service-item-mobile'>Preposlanie obsahu listovch zsielok mailom (otvoren)  <span id='small-item-long' style={{
          fontSize: '0.8rem',
          fontWeight: 'unset',
        }}>1x tdenne</span></div>
                </div>}
                </div>}
              <div className='service-item-mobile services-item--last'>Cena spolu / mesiac: {option} &euro;</div>
              <div className="center-button">
                <button className='click check-click' onClick={ () => navigateToPage(selectButtonPath(option))}>Mm zujem</button>
              </div>
            </div>         
      </div>
      <div className='note mobile-display'>
        <p className='note-p'>* Plat sa paulne rone, vopred</p>
        <p className='note-p'>** Registrcia sdla / miesta podnikania min. na 12 mesiacov</p>
      </div>

      <div className='mobile-table mobile-display space'>
        <div className='assistantPrice-item-mobile services-item--first'>Vedenie utovnctva a sluby virtulnej asistentky</div>
        <div className='assistantPrice-item-mobile'>Cennk je stanoven individulne po zadan loh a zistenia nronosti prce ***</div>
      </div>
      
      <div className='note mobile-display'>
        <p className='note-p' id="quotes">*** Ak sluba podlieha nkladom vynaloench predom (napr. poplatok na katastri), tieto poplatky su vybran od klienta predom. V ostatnch prpadoch je faktra dodan a po vykonan prc.</p>
      </div>
      
    </div>

  )
}

export default PriceList;