import React, { useState } from "react";
import Menu from "../menu/Menu";
import Foot from "../foot/Foot";
import Formular from "../formular/Formular";
import yes from "../../images/yes.png";
import Ad from "../Ad/Ad";
import CookieConsent from "react-cookie-consent";

function ExpandedPackagePage(){
  
  const [isOrdered, setIsOrdered] = useState(false);
  const [mobileMenu, setMobileMenu]= useState(false);

  return(
    <div>
      <Menu mobileMenu={mobileMenu} setMobileMenu={setMobileMenu}/>
      <div className={!isOrdered? "main-title title-margin" : "enable-display"}>Rozren balk<span className="mobile-item mobile-title"> - zhrnutie</span></div>
      <div className={!isOrdered? "content-box": "enable-display"}>
        <div className="summary-box">
          <div className="summary-list">
            <ul className='services-list summary-01'>
                <li className='services-item'>Shlas so zriadenm sdla / miesta podnikania</li>
                <li className='services-item'>Oznaenie potovej schrnky</li>
                <li className='services-item'>Preberanie listovch zsielok (obyajnch)</li>
                <li className='services-item'>Archivcia neprevzatch dokladov max. 1 rok</li>
                <li className='services-item'>Archivcia neprevzatch dokladov max. 1 rok</li>
                <li className='services-item services-item--long'>Preposlanie listovch zsielok mailom<br/>(neotvoren)  <span id='small-item-long' style={{
          fontSize: '0.8rem',
          fontWeight: 'unset',
        }}>1x tdenne</span></li>
                <li className='services-item services-item--long'>Preposlanie lstku ouloen zsielky na pote<br/>mailom  <span id='small-item-long' style={{
          fontSize: '0.8rem',
          fontWeight: 'unset',
        }}>1x tdenne</span></li>
                <li className='services-item services-item--long'>Preposlanie listovch zsielok na dohodnut<br/>adresu (doporuene)  <span id='small-item-long' style={{
          fontSize: '0.8rem',
          fontWeight: 'unset',
        }}>2x mesane</span></li>
                <li className='services-item services-item--long'>Preposlanie obsahu listovch zsielok mailom<br/>(otvoren)  <span id='small-item-long' style={{
          fontSize: '0.8rem',
          fontWeight: 'unset',
        }}>1x tdenne</span></li>
                <li className='services-item services-item--last'>Cena spolu / mesiac<span className="mobile-item">: 13 &euro;</span></li>
                <li className='services-item services-item--last'>Cena spolu / rok<span className="mobile-item">: 156 &euro;</span></li>
              </ul>

              <ul className='check-list-01 summary-02'>
                <li className='check-item '><img className='check-img' src={yes} /></li>
                <li className='check-item '><img className='check-img' src={yes} /></li>
                <li className='check-item'><img className='check-img' src={yes} /></li>
                <li className='check-item'><img className='check-img' src={yes} /></li>
                <li className='check-item'><img className='check-img' src={yes} /></li>
                <li className='check-item services-item--long'><img className='check-img' src={yes} /></li>
                <li className='check-item services-item--long'><img className='check-img' src={yes} /></li>
                <li className='check-item services-item--long'><img className='check-img' src={yes} /></li>
                <li className='check-item services-item--long'><img className='check-img' src={yes} /></li>
                <li className='check-item check-item--last'>13 &euro;</li>
                <li className='check-item check-item--last'>156 &euro;</li>
              </ul>
          </div>
        </div>
        <Formular package="zkladn" isOrdered={isOrdered} setIsOrdered={setIsOrdered}/>
      </div>
      <div className={isOrdered? "half-height" : ""}>
        <div className={isOrdered? "main-title title-center": "enable-display"}>akujeme za objednvku</div>
        <p className={isOrdered? "title text-center": "enable-display"}>Poslali sme Vm potvrdzujci email</p>
        <Ad/>
      </div>

      
      <Foot bottom={isOrdered}/>
      <CookieConsent
        location="bottom"
        buttonText="Rozumiem"
        disableStyles={true}
        style={{ background: "#2B373B", color: "var(--main-color-orange)", textAlign: "center", display: "flex", flexDirection: "column", position: "fixed", width: "100%",  alignItems: "center", padding: "0.5rem 0", }}
        buttonStyle={{ backgroundColor: "var(--main-color-orange)", textAlign: "center", color: "var(--main-color-brown)", fontSize: "1rem", width: "5.6rem", height: "1.7rem", border: "none", borderRadius: "0.5rem", margin: "0.5rem 0 0.2rem 0", cursor: "pointer"  }}
        expires={365}
        >
        Prevdzkovate oboznamuje Uvatea i Nvtevnka, e webov strnka pouva len zkladn (funkn) sbory Cookies. Bez tchto Cookies nememe poskytova sluby, ktor tvoria zklad naej strnky. Pokia tieto Cookies zakete, nebudeme mc zabezpei bezchybn chod strnky. In Cookies nezbierame.
      </CookieConsent>
    </div>
  )
};

export default ExpandedPackagePage;