import React from "react";
import CookieConsent from "react-cookie-consent";

function CookiesPage(){

    return(
        <div>
            <div className="personalData-box">
                <div className="main-title title-light">Cookies</div>
                <p className="personalData-text">Poskytovate oboznamuje Objednvatea a/alebo Uvatea i Nvtevnka tejto internetovej
strnky, e na strnke sa pouvaj len zkladn (funkn) sbory cookies. S to sbory, ktor
nepokodzuj v pota / in mdium, neobsahuj vrusy. Tieto sbory s nevyhnutn, aby ste
sa mohli pohybova po naej internetovej strnke a vyuva jej funkcie. Spomnan zkladn
sbory cookies tvoria zklad pre prevdzku naej internetovej strnky a umouj pouvanie
zkladnch funkci. K zkladnm cookies patria naprklad predvyplnenie formulrov.<br/><br/>
Bez tchto cookies nememe poskytova sluby, ktor tvoria zklad naej internetovej strnky.
Pokia tieto cookies zakete, nebudeme mc zabezpei bezchybn chod naej internetovej
strnky. In cookies nezbierame. Spomnan sbory cookies o vs nezhromauj iadne
informcie na marketingov, preferenn, tatistick ely, ani in okrem tch vyie uvedench.
Stle ale mte monos zabrni zbieraniu naich zkladnch sborov cookies a to tak, e bu
opustte nau internetov strnku, alebo zmenou nastavenia vo svojom prehliadai.<br/><br/>
Zrove upozorujeme Objednvatea, Uvatea i Nvtevnka, e na naej internetovej
strnke s odkazy na in internetov strnky, ktor mu zbiera aj in ako zkladn (funkn)
sbory cookies. Prekliknutm sa na in internetov strnku z naej internetovej strnky vs
dan strnka vyzve k povoleniu ich vlastnch cookies.<br/><br/>
</p>
<p className="personalData-text space-top">Zverejnen .................................., s innosou od .................................., na dobu neurit.</p>
            </div>
            <CookieConsent
                location="bottom"
                buttonText="Rozumiem"
                disableStyles={true}
                style={{ background: "#2B373B", color: "var(--main-color-orange)", textAlign: "center", display: "flex", flexDirection: "column", position: "fixed", width: "100%",  alignItems: "center", padding: "0.5rem 0", }}
                buttonStyle={{ backgroundColor: "var(--main-color-orange)", textAlign: "center", color: "var(--main-color-brown)", fontSize: "1rem", width: "5.6rem", height: "1.7rem", border: "none", borderRadius: "0.5rem", margin: "0.5rem 0 0.2rem 0", cursor: "pointer"  }}
                expires={365}
                >
                Prevdzkovate oboznamuje Uvatea i Nvtevnka, e webov strnka pouva len zkladn (funkn) sbory Cookies. Bez tchto Cookies nememe poskytova sluby, ktor tvoria zklad naej strnky. Pokia tieto Cookies zakete, nebudeme mc zabezpei bezchybn chod strnky. In Cookies nezbierame.
            </CookieConsent>
        </div>
    )

}

export default CookiesPage